	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>Pulse Film Festival is a month-long festival\nthat plays thriller/horror genre movies on\nFriday and Saturday nights throughout\nOctober. The festival wants to elicit feelings of unease\nwith their marketing material, and make the\nviewer’s pulse quicken.</p>\n<p>By layering sounds and patterns, there is a growing sense of unease and\ntrepidation that results in a climax that\nabruptly breaks off into the festival branding.\nBlack and white were used to symbolize the\nhigh contrast often found in the thriller/\nhorror genre (playing off fear of the dark),\nwith splashes of red to represent the often\ngory themes.</p>\n<p>By using four checkerboard patterns stacked\none on top of the other, I was able to create a\nvariety of textures by transforming the layers\nseparately. The typeface, Lemon Milk, was used\nbecause of its striking angles, playing off of\nthe pulse beat.</p>\n<p>youtube mKhtNyyPSkY</p>","frontmatter":{"title":"Pulse Film Festival","graduate":"Steph Hoekstra","category":"Motion Design","category2":"Typography","category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/hoekstra_steph_pulseff-9701090ecedddf71aa53ae468896692b-a7a62.jpg","srcSet":"/static/hoekstra_steph_pulseff-9701090ecedddf71aa53ae468896692b-5a1a9.jpg 375w,\n/static/hoekstra_steph_pulseff-9701090ecedddf71aa53ae468896692b-0908c.jpg 750w,\n/static/hoekstra_steph_pulseff-9701090ecedddf71aa53ae468896692b-a7a62.jpg 1500w,\n/static/hoekstra_steph_pulseff-9701090ecedddf71aa53ae468896692b-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/pulse-film-festival/","name":"/Pulse Film Festival/"}}

